import React, { useState } from "react";
import Modal from "./modal";
import { useDispatch, useSelector } from "react-redux";
import {
  REGISTER_MODAL,
  setModalVisibility,
  LOGIN_MODAL
} from "../store/actions/modalActions";
import { register } from "../store/actions/authActions";
import { toast } from "react-toastify";
export default function RegisterModal() {
  const dispatch = useDispatch();
  const { isRegisterOpen } = useSelector(state => state.modal);
  const [nick, setNick] = useState("");
  const [passwd, setPasswd] = useState("");
  const [passwdAgain, setPasswdAgain] = useState("");
  const [email, setEmail] = useState("");
  const [errors, setErrors] = useState([]);
  const registerHandle = e => {
    e.preventDefault();

    const errors = [];
    if (!nick || !passwd || !passwdAgain || !email)
      errors.push("BoÅ alan bÄ±rakmayÄ±n.");

    if (nick.length > 15) errors.push("Nick 15 karakterden fazla olamaz.");

    if (passwd !== passwdAgain) errors.push("Åifreler uyuÅmuyor.");

    if (!email.includes("@")) errors.push("GeÃ§erli eposta adresi girin");

    setErrors(errors);
    if (errors.length > 0) {
      return;
    }

    dispatch(register(nick, passwd, email));
  };
  return (
    <Modal isOpen={isRegisterOpen} modal={REGISTER_MODAL}>
      <div className="register-modal modal-content">
        <ul>
          {errors.map((e, i) => (
            <li key={i}>{e}</li>
          ))}
        </ul>
        <div className="form-wrapper">
          <form>
            <input
              type="text"
              value={nick}
              onChange={e => setNick(e.target.value)}
              className="default-input"
              placeholder="kullanÄ±cÄ± adÄ± giriniz"
              required
              maxLength={15}
            />
            <input
              type="password"
              value={passwd}
              onChange={e => setPasswd(e.target.value)}
              className="default-input"
              placeholder="Åifre giriniz"
              required
              minLength={6}
            />
            <input
              type="password"
              value={passwdAgain}
              onChange={e => setPasswdAgain(e.target.value)}
              className="default-input"
              placeholder="tekrar Åifre giriniz"
              required
            />
            <input
              type="email"
              value={email}
              onChange={e => setEmail(e.target.value)}
              className="default-input"
              placeholder="eposta adresi giriniz"
              required
            />
            <button
              className="default-btn btn-register"
              onClick={registerHandle}
            >
              kayÄ±t ol
            </button>
          </form>
        </div>
        <p className="already-member">
          zaten hesabÄ±n var mÄ±?
          <button
            className="default-btn"
            onClick={() => dispatch(setModalVisibility(LOGIN_MODAL, true))}
          >
            giriÅ yap
          </button>
        </p>
        <p>KayÄ±t olarak kullanÄ±cÄ± sÃ¶zleÅmesini kabul etmiÅ olursunuz.</p>
      </div>
    </Modal>
  );
}
